.signupback{
    height: 100vh;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}
.signupinner{
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: lightblue;
    border-radius: 10px;
    text-align: center;
    overflow: hidden;
}
.signupinner .imgdiv{
    background-color: red;
    width: 560px;
    height: 450px;
    background-image: url("../image/1.jpg");
    background-size: cover;
}
.signupinner .formdiv{
    width: 400px;
}
.signupinner .formdiv h4{
    font-family: sans-serif;
    margin: 10px 0px 30px;
}
.signupinner .formdiv input{
    margin: 20px 10px;
}