.pop-data-main{
    /* background-color: yellow; */
    width: 100%;
    height: 80vh;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 10px 0px 10px 100px;
    overflow: hidden;
    
}

.pop-data{
    /* background-color: red; */
    width: 600px;
    margin-left: -150px;
}
.pop-data h1{
    font-size: 25px;
}
.pop-image{
    width: 700px;
}
.pop-image img{
    width: 70%;
}
.pop-enter{
    border: 2px solid black;
    display: flex;
    justify-content: center;
    align-items: center;
    /* box-shadow: 0px 0px 0px 0px black; */
    position: relative;
}
.pop-p{
    position: absolute;
    left: 96%;
    top: 0px;
    font-size: 36px;
    color: rgb(72, 9, 9);
}
.slide-pop img{
    width: 70%;
}

.buynowbox{
    /* background-color: red; */
    border: 1px solid ;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 70px 300px;
    padding: 30px 0px;
}
.buynow-image{
    /* background-color: yellow; */
    width: 35%;
    text-align: center;
}
.buynow-image img{
    width: 300px;
}
.buynow-image h6{
font-size: 30px;
padding: 10px 0;
}
.buynow-data{
    /* background-color: gray; */
    width: 50%;
   display: flex;
   justify-content: center;
   align-items: center;
}
.buynowform li{
list-style: none;
}
.buynowform input{
    background-color: transparent;
    border: none;
    border-bottom: 1px solid black;
   margin-top: -90px;
   margin-bottom: 20px;
   outline: none;
}
